* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.gallery > ul {
  display: flex;
  flex-wrap: wrap;
  gap: 1vw;
}
.photo-card {
  list-style-type: none;
  width: 22vw;
  height: 22vw;
  margin: 0;
  padding: 0;
}

.info {
  display: flex;
  font-size: 1vw;
  text-align: center;
  margin: 0;
  padding: 0;
}

img {
  object-fit: cover;
  width: 100%;
  height: 80%;
}
